html, body, #root, #root>.App {
   height: 100%;
   width: 100%;
   margin: 0;
   padding: 0;
}

body {
	font-size: 13px;
	font-family: "Open Sans", sans-serif;
	color: #bbb;
}

#sketch-view {
	position: absolute;
	height: 100%;
	width: 100%;
  top: 0;
  left: 0;
	border: none;
	margin: 0;
	padding: 0
}

.carousel {
  z-index: 10 !important;
	position: absolute;
	bottom: 20px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  height: 190px;
  width: 70%;
}

.carousel-content { z-index: 13 !important; text-align: center !important; }



