.carousel {
  opacity: 1;
  -webkit-transition-duration: 0.3s;
       -o-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-property: opacity;
  -o-transition-property: opacity;
  transition-property: opacity;
}

.carousel.carousel-empty { opacity: 0 !important; }

.carousel-scroller, .carousel-content {
  width: 100%;
  height: 100%;
  position: absolute;
  padding: 0;
  margin: 0;
  text-align: left;
  white-space: nowrap;
}

.carousel-scroller { overflow: hidden; }
.carousel-content { width: auto !important; }

.carousel-item {
  display: inline-block;
  margin: 0 5px;
  height: 100%;
  width: auto !important;
}

.carousel-controls {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.carousel-controls.hide-controls { display: none; }

.carousel-controls .carousel-btn {
  position: absolute;
  top: 50%;
  -webkit-transform: translate(0, -50%);
      -ms-transform: translate(0, -50%);
          transform: translate(0, -50%);
}

.carousel-prev { left: -50px; }
.carousel-next { right: -50px; }



